import React from "react";
import Slider from "react-slick";

const story = [
  {
    name: "Cherice Justin",
    username: "@cherice.me",
    story: "Ikut serta dalam event jejepangan di Bandung adalah pengalaman tak terlupakan! Kostum dan komunitasnya luar biasa.",
    image: "https://via.placeholder.com/50",
  },
  {
    name: "Minnie Horn",
    username: "@hello.mimmie",
    story: "Event cosplay di Jakarta benar-benar seru! Banyak teman baru dengan hobi yang sama. Event cosplay di Jakarta benar-benar seru! Banyak teman baru dengan hobi yang sama.",
    image: "https://via.placeholder.com/50",
  },
  {
    name: "Thais Carballal",
    username: "@myself.thais",
    story: "Berpartisipasi dalam lomba karaoke jejepangan membuat saya merasa diterima dan dihargai.",
    image: "https://via.placeholder.com/50",
  },
  {
    name: "Veona Watson",
    username: "@hi.veona",
    story: "Saya belajar banyak dari workshop seni visual di event Jejepangan tahun lalu. Sangat inspiratif!",
    image: "https://via.placeholder.com/50",
  },
  {
    name: "Cherice Justin",
    username: "@cherice.me",
    story: "Ikut serta dalam event jejepangan di Bandung adalah pengalaman tak terlupakan! Kostum dan komunitasnya luar biasa.Ikut serta dalam event jejepangan di Bandung adalah pengalaman tak terlupakan! Kostum dan komunitasnya luar biasa.",
    image: "https://via.placeholder.com/50",
  },
  {
    name: "Minnie Horn",
    username: "@hello.mimmie",
    story: "Event cosplay di Jakarta benar-benar seru! Banyak teman baru dengan hobi yang sama.",
    image: "https://via.placeholder.com/50",
  },
  {
    name: "Thais Carballal",
    username: "@myself.thais",
    story: "Berpartisipasi dalam lomba karaoke jejepangan membuat saya merasa diterima dan dihargai. Berpartisipasi dalam lomba karaoke jejepangan membuat saya merasa diterima dan dihargai.",
    image: "https://via.placeholder.com/50",
  },
  {
    name: "Veona Watson",
    username: "@hi.veona",
    story: "Saya belajar banyak dari workshop seni visual di event Jejepangan tahun lalu. Sangat inspiratif!",
    image: "https://via.placeholder.com/50",
  },
];

const Stories = () => {
  const settings = {
    infinite: true,
    slidesToShow: 3,
    slidesToScroll: 1,
    centerMode: true,
    centerPadding: "160px",
    swipeToSlide: true,
    draggable: true,
    arrows: false,
    autoplay: true,
    speed: 500,
    autoplaySpeed: 3000,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          centerPadding: "160px",
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          centerPadding: "0px",
        },
      },
    ],
  };

  // Group story into pairs
  const groupedStories = story.reduce((acc, _, index, arr) => {
    if (index % 2 === 0) acc.push(arr.slice(index, index + 2));
    return acc;
  }, []);

  return (
    <section className="py-24 bg-gray-100 text-center">
      <h2 className="text-4xl font-bold text-gray-800 mb-2">
      Cerita dari Kawan
      </h2>
      <p className="text-gray-600 mb-8">Simak pengalaman menarik dari komunitas yang telah bergabung di berbagai event jejepangan.</p>
      <Slider {...settings}>
        {groupedStories.map((group, index) => (
          <div key={index} className="flex flex-col justify-center items-start gap-6">
            {group.map((item, i) => (
              <div
                key={i}
                className="bg-white rounded-lg p-6 max-w-lg mx-4 md:mx-2 mt-4"
              >
                <p className="text-gray-700 text-sm mb-4 text-start">{item.story}</p>
                <div className="flex items-center">
                  <img
                    src={item.image}
                    alt={item.name}
                    className="w-12 h-12 rounded-full mr-4"
                  />
                  <div>
                    <h4 className="text-md font-bold text-gray-800 text-start">
                      {item.name}
                    </h4>
                    <p className="text-xs font-bold text-gray-500 text-start text-indigo-400">{item.username}</p>
                  </div>
                </div>
              </div>
            ))}
          </div>
        ))}
      </Slider>
    </section>
  );
};

export default Stories;
