import React, { useState } from "react";

const TabbedFeatures = () => {
  const [activeTab, setActiveTab] = useState(0); // Active tab
  const [isAnimating, setIsAnimating] = useState(false); // Animation status

  const tabs = [
    {
      icon: <i className="bi bi-suit-heart-fill"></i>,
      title: "Seni Cosplay",
      image: "https://via.placeholder.com/1200x600",
    },
    {
      icon: <i className="bi bi-camera-fill"></i>,
      title: "Fotografi",
      image: "https://via.placeholder.com/1200x600",
    },
    {
      icon: <i className="bi bi-music-note-beamed"></i>,
      title: "Seni Musik",
      image: "https://via.placeholder.com/1200x600",
    },
    {
      icon: <i className="bi bi-brush-fill"></i>,
      title: "Seni Visual",
      image: "https://via.placeholder.com/1200x600",
    },
  ];

  const handleTabChange = (index) => {
    if (index === activeTab || isAnimating) return; // Prevent switching during animation
    setIsAnimating(true);

    setTimeout(() => {
      setActiveTab(index); // Switch tab after fade-out animation
      setIsAnimating(false); // Animation completed
    }, 300); // Animation duration
  };

  return (
    <section className="bg-gradient-to-b from-gray-50 via-purple-100 to-indigo-100 py-16 px-4 sm:px-6 lg:px-8">
      {/* Header */}
      <div className="text-center mb-16 mt-8">
        <h2 className="text-4xl font-bold mb-4 md:text-3xl">
          Tuangkan Hobby Kamu
        </h2>
        <p className="text-gray-600 text-sm sm:text-base">
          Ekspresikan hobby kamu, mari tunjukkan yang terbaik dari kita
        </p>
      </div>

      {/* Tabs */}
      <div className="max-w-screen-lg mx-auto flex flex-wrap justify-center gap-6 mb-12">
        {tabs.map((tab, index) => (
          <button
            key={index}
            className={`relative flex items-center gap-4 py-3 px-6 rounded-lg text-sm sm:text-base font-medium transition-all duration-300 ${
              activeTab === index
                ? "bg-gradient-to-r from-purple-600 to-indigo-500 text-white shadow-lg transform scale-105"
                : "bg-white text-gray-600 border border-gray-300 hover:shadow-md hover:text-gray-800"
            }`}
            onClick={() => handleTabChange(index)}
          >
            {/* Icon */}
            <span
              className={`text-xl sm:text-2xl transition-transform duration-500 ${
                activeTab === index
                  ? "text-white scale-110"
                  : "text-gray-500 hover:text-gray-800"
              }`}
            >
              {tab.icon}
            </span>

            {/* Title */}
            <span
              className={`transition-opacity duration-300 ${
                activeTab === index ? "opacity-100" : "opacity-50"
              }`}
            >
              {tab.title}
            </span>
          </button>
        ))}
      </div>

      {/* Tab Content */}
      <div className="max-w-screen-xl mx-auto flex justify-center">
        <div
          className={`transition-all duration-500 transform ${
            isAnimating ? "opacity-0 scale-95" : "opacity-100 scale-100"
          }`}
        >
          <img
            src={tabs[activeTab].image}
            alt={tabs[activeTab].title}
            className="rounded-xl shadow-lg w-full h-auto max-w-full"
          />
        </div>
      </div>
    </section>
  );
};

export default TabbedFeatures;
