import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { useMediaQuery } from "react-responsive";
import { FastAverageColor } from "fast-average-color";
import slide1 from "../assets/slide1.jpg";
import slide2 from "../assets/slide2.jpg";
import slide3 from "../assets/slide3.jpg";
import slide4 from "../assets/slide4.jpg";

// Data untuk carousel
const slides = [
  {
    id: 1,
    image: slide1,
    title: "Cosplayer Event",
    description: "Join us for an amazing cosplay event full of fun and creativity.",
    alt: "Slide 1",
  },
  {
    id: 2,
    image: slide2,
    title: "Anime Expo",
    description: "Discover the latest trends and meet fellow anime enthusiasts.",
    alt: "Slide 2",
  },
  {
    id: 3,
    image: slide3,
    title: "Comic Con",
    description: "Experience the ultimate comic con event with exciting panels.",
    alt: "Slide 3",
  },
  {
    id: 4,
    image: slide4,
    title: "Gaming Fest",
    description: "Dive into the world of gaming with tournaments and prizes.",
    alt: "Slide 4",
  },
];

const HeroCarousel = () => {
  const isMobile = useMediaQuery({ query: "(max-width: 768px)" });
  const [bgColor1, setBgColor1] = useState("#000"); // Overlay pertama
  const [bgColor2, setBgColor2] = useState("#000"); // Overlay kedua
  const [activeOverlay, setActiveOverlay] = useState(1); // Overlay aktif

  const settings = {
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    centerMode: true,
    centerPadding: isMobile ? "0px" : "320px",
    swipeToSlide: true,
    draggable: true,
    arrows: false,
    autoplay: true,
    speed: 500,
    autoplaySpeed: 3000,
    beforeChange: (_, next) => handleColorChange(slides[next].image),
    responsive: [
        {
            breakpoint: 1280,
            settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            centerPadding: "160px",
            },
        },
        {
            breakpoint: 768,
            settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            centerPadding: "0px",
            },
        },
    ],
  };

  // Fungsi untuk mengambil warna rata-rata dari gambar
  const handleColorChange = (imageURL) => {
    const fac = new FastAverageColor();
    const img = new Image();
    img.crossOrigin = "anonymous";
    img.src = imageURL;

    img.onload = () => {
      const color = fac.getColor(img).rgb;

      // Swap active overlay
      if (activeOverlay === 1) {
        setBgColor2(color); // Update overlay kedua
        setActiveOverlay(2); // Aktifkan overlay kedua
      } else {
        setBgColor1(color); // Update overlay pertama
        setActiveOverlay(1); // Aktifkan overlay pertama
      }
    };

    img.onerror = () => {
      console.error("Failed to load image for color extraction:", imageURL);
    };
  };

  useEffect(() => {
    // Set initial color based on the first slide
    handleColorChange(slides[0].image);
  }, []);

  return (
    <section className="mt-10 sm:mt-0">
      {/* Gradient Overlay 1 */}
      <div
        className={`absolute inset-0 z-1 transition-opacity duration-1000 ease-in-out ${
          activeOverlay === 1 ? "opacity-100" : "opacity-0"
        }`}
        style={{
          background: `linear-gradient(180deg, ${bgColor1} 0%, transparent 100%)`,
        }}
      ></div>

      {/* Gradient Overlay 2 */}
      <div
        className={`absolute inset-0 z-1 transition-opacity duration-1000 ease-in-out ${
          activeOverlay === 2 ? "opacity-100" : "opacity-0"
        }`}
        style={{
          background: `linear-gradient(180deg, ${bgColor2} 0%, transparent 100%)`,
        }}
      ></div>

      {/* Carousel */}
      <Slider {...settings}>
        {slides.map((slide) => (
          <div
            key={slide.id}
            className={`relative group flex justify-center items-center ${
              isMobile ? "px-4" : "px-6"
            }`}
          >
            {/* Slide Content with Overlay and Hover Effects */}
            <div
              className={`relative rounded-xl overflow-hidden ${
                isMobile ? "" : ""
              }`}
            >
              {/* Image with Zoom-In Effect */}
              <img
                src={slide.image}
                alt={slide.alt}
                className={`rounded-xl transition-transform duration-500 ease-in-out transform ${
                  isMobile ? "group-hover:scale-105" : "group-hover:scale-110"
                } hero-banner`}
              />

              {/* Overlay */}
              <div
                className={`absolute inset-0 ${
                  isMobile
                    ? "bg-gradient-to-t from-black via-transparent to-neutral-800"
                    : "bg-gradient-to-t from-black via-transparent to-neutral-800"
                } opacity-50 group-hover:opacity-70 transition-all duration-500 ease-in-out`}
              ></div>

              {/* Title and Description */}
              <div
                className={`absolute ${
                  isMobile ? "bottom-6 left-4" : "bottom-12 left-12"
                } text-white opacity-0 group-hover:opacity-100 transition-opacity duration-500 ease-in-out`}
              >
                <h3
                  className={`${
                    isMobile ? "text-2xl" : "text-4xl md:text-6xl"
                  } font-bold text-neutral-100`}
                >
                  {slide.title}
                </h3>
                <p className={`mt-2 ${isMobile ? "text-sm" : "text-md"} text-neutral-300`}>
                  {slide.description}
                </p>
              </div>
            </div>
          </div>
        ))}
      </Slider>
    </section>
  );
};

export default HeroCarousel;
