import React from "react";

const Intro = () => {
  return (
    <section className="relative bg-gradient-to-b from-purple-600 to-indigo-900 text-white py-20">
      {/* Background Decorations */}
      <div className="absolute inset-0 z-0">
        <div className="absolute top-10 left-10 w-40 h-40 bg-blue-500 opacity-30 rounded-full blur-3xl"></div>
        <div className="absolute bottom-10 right-10 w-40 h-40 bg-purple-500 opacity-30 rounded-full blur-3xl"></div>
      </div>

      {/* Content */}
      <div className="relative z-10 text-center max-w-4xl mx-auto">
        <h1 className="text-4xl sm:text-5xl font-extrabold leading-tight">
          Selamat Datang di <span className="text-yellow-300">Komunitas Jejepangan Indonesia</span>
        </h1>
        <p className="mt-4 text-lg sm:text-xl text-gray-200">
          Temukan teman-teman yang memiliki kesukaan yang sama, rayakan hobi tanpa rasa malu, dan jadi bagian dari komunitas yang mendukung kreativitas kita semua.
        </p>
        
        {/* Call to Action Buttons */}
        <div className="mt-8 flex justify-center gap-4">
          <a
            href="#join"
            className="bg-yellow-500 hover:bg-yellow-600 text-black py-3 px-6 rounded-lg text-lg font-semibold transition-all duration-300"
          >
            Bergabung Sekarang
          </a>
          <a
            href="#learn-more"
            className="border-2 border-gray-200 hover:border-yellow-500 py-3 px-6 rounded-lg text-lg font-semibold text-gray-200 hover:text-white transition-all duration-300"
          >
            Pelajari Lebih Lanjut
          </a>
        </div>
      </div>
    </section>
  );
};

export default Intro;