import React from "react";

const UpcomingEvents = () => {
  const events = [
    {
      date: "2024-12-25",
      time: "10:00 AM",
      location: "Bandung Electronic Centre, Bandung Utara, Jawa Barat, Indonesia",
      title: "Perayaan Natal",
      description: "Bergabunglah dengan kami dalam kemeriahan Natal yang penuh musik, permainan, dan kebahagiaan!",
      image: "https://via.placeholder.com/600x400",
    },
    {
      date: "2025-01-01",
      time: "12:00 AM",
      location: "Times Square, New York",
      title: "Countdown Tahun Baru",
      description: "Sambut tahun baru dengan penuh semangat di Times Square bersama ribuan orang lainnya.",
      image: "https://via.placeholder.com/600x400",
    },
    {
      date: "2025-02-14",
      time: "6:00 PM",
      location: "Central Park, New York",
      title: "Spesial Hari Valentine",
      description: "Rayakan cinta dengan malam romantis di Central Park bersama pasangan Anda.",
      image: "https://via.placeholder.com/600x400",
    },
    {
      date: "2025-02-21",
      time: "6:00 PM",
      location: "Central Park, New York",
      title: "Spesial Hari White Valentine",
      description: "Rayakan cinta dengan malam romantis di Central Park bersama pasangan Anda.",
      image: "https://via.placeholder.com/600x400",
    },
  ];

  return (
    <section className="pt-16 pb-32 bg-gray-50 -mt-40">
      {/* Section Header */}
      <div className="mb-20 mt-32 text-center px-8">
        <h2 className="text-3xl sm:text-4xl font-bold text-gray-800 mb-6">Event Mendatang</h2>
        <p className="text-lg text-gray-600 mb-8 max-w-3xl mx-auto">
          Temukan informasi terbaru tentang berbagai event jejepangan di seluruh Indonesia. Kami menyediakan informasi lengkap untuk semua event menarik.
        </p>
      </div>

      {/* Event Cards */}
      <div className="grid sm:grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-12 max-w-screen-xl mx-auto px-6">
        {events.map((event, index) => (
          <div
            key={index}
            className="overflow-hidden bg-white shadow-lg rounded-lg transition-transform transform hover:scale-105"
          >
            {/* Event Image */}
            <img
              src={event.image}
              alt={event.title}
              className="w-full h-72 object-cover"
            />

            {/* Event Details */}
            <div className="p-6">
              {/* Date & Time */}
              <div className="flex items-center text-gray-500 text-sm mb-4">
                <span className="flex items-center gap-2">
                  <i className="bi bi-calendar3 text-xl"></i> {new Date(event.date).toLocaleDateString("id-ID", {
                    day: "numeric",
                    month: "long",
                    year: "numeric",
                  })}
                </span>
                <span className="mx-4">|</span>
                <span className="flex items-center gap-2">
                  <i className="bi bi-clock text-xl"></i> {event.time}
                </span>
              </div>

              {/* Title */}
              <h3 className="text-2xl font-semibold text-gray-800 mb-2">
                {event.title}
              </h3>

              {/* Description */}
              <p className="text-gray-600 mb-4 text-sm">
                {event.description}
              </p>

              {/* Location */}
              <div className="flex items-center text-gray-500 text-sm gap-2">
                <i className="bi bi-geo-alt-fill text-xl"></i>
                {event.location}
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* CTA Button */}
      <div className="text-center mt-12">
        <button className="px-6 py-3 bg-gradient-to-r from-purple-600 to-indigo-500 text-white font-medium rounded-lg hover:shadow-lg transition-all duration-500">
          Lihat Event Lainnya &rarr;
        </button>
      </div>
    </section>
  );
};

export default UpcomingEvents;
