import React, { useState } from "react";

const FAQSection = () => {
  const [activeIndex, setActiveIndex] = useState(null);

  const faqs = [
    {
      question: "Apa itu komunitas jejepangan Indonesia?",
      answer:
        "Komunitas Jejepangan Indonesia adalah wadah untuk para penggemar budaya Jepang seperti anime, manga, cosplay, musik, dan lainnya. Kami menyediakan informasi acara, tips, dan ruang diskusi untuk semua penggemar di Indonesia.",
    },
    {
      question: "Bagaimana cara bergabung dengan komunitas ini?",
      answer:
        "Menjadi anggota komunitas ini gratis. Kamu dapat bergabung dengan mendaftar melalui halaman utama website kami.",
    },
    {
      question: "Apakah komunitas ini tertutup bagi komunitas lain di Indonesia?",
      answer:
        "Tidak. Kami menyambut hangat semua Komunitas Jejepangan yang ada Indonesia. Juga bagi kamu yang tidak terlibat dalam komunitas manapun, jangan takut untuk menghubungi kami. Silahkan menghubungi kami melalui halaman Help & Support atau email! ",
    },
    {
      question: "Bagaimana cara berkontribusi pada komunitas?",
      answer:
        "Kamu dapat berkontribusi dengan membagikan cerita atau pengalaman kamu di acara jejepangan, menjadi sukarelawan di website komunitas ini sebagai moderator, atau berpartisipasi dalam diskusi di forum kami.",
    },
    {
      question: "Bagaimana uang donasi digunakan?",
      answer:
        "Untuk sekarang semua uang donasi kami alokasikan ke biaya pengembangan website seperti biaya sewa hosting, biaya sewa domain, dan juga biaya operasional lain lain.",
    },
  ];

  const toggleFAQ = (index) => {
    setActiveIndex(activeIndex === index ? null : index);
  };

  return (
    <section className="relative pt-16 pb-32 bg-gray-50 text-center">
      <h2 className="text-4xl font-bold mb-4 text-gray-800">
        Pertanyaan yang Sering Diajukan
      </h2>
      <p className="text-gray-600 mb-12">
        Temukan jawaban untuk pertanyaan Anda tentang komunitas ini.
      </p>
      <div className="max-w-screen-xl mx-auto px-4 flex flex-col-reverse md:flex-row gap-12">
        {/* Left Section */}
        <div className="md:w-1/3 flex flex-col">
          <h3 className="text-2xl md:text-3xl font-bold text-gray-800 mb-4 text-start">
            Punya pertanyaan lainnya? Kami siap membantu.
          </h3>
          <p className="text-gray-600 mb-6 text-start">
            Jika pertanyaan Anda tidak tercantum di sini, jangan ragu untuk
            mengajukan pertanyaan melalui formulir bantuan kami.
          </p>
          <div className="flex">
            <button className="px-6 py-3 bg-gray-800 text-white rounded-lg font-semibold transition duration-500 hover:bg-gray-900">
              Ajukan Pertanyaan
            </button>
          </div>
        </div>

        {/* FAQ Section */}
        <div className="md:w-2/3">
          <div className="space-y-4">
            {faqs.map((faq, index) => (
              <div
                key={index}
                className="border border-gray-200 rounded-lg overflow-hidden"
              >
                <button
                  onClick={() => toggleFAQ(index)}
                  className="w-full flex justify-between items-center px-4 py-3 text-left text-gray-800 font-medium hover:bg-gray-100 transition-all duration-300"
                >
                  {faq.question}
                  <span className="ml-2 text-gray-500">
                    {activeIndex === index ? (
                      <i className="bi bi-chevron-down"></i>
                    ) : (
                      <i className="bi bi-chevron-right"></i>
                    )}
                  </span>
                </button>
                {activeIndex === index && (
                  <div className="px-4 py-3 text-gray-600 text-start">
                    {faq.answer}
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default FAQSection;
